.module-selector-overlay {
  &.modal-mask {
    display: none;
    &.active {
      display: table;
      opacity: 1;
    }

  }
  //position: fixed;
  //top: 0;
  //left: 0;
  //width: 100vw;
  //height: 100vh;
  //background: rgba(255, 255, 255, 0.8);
  display: none;
  //align-items: center;
  //justify-content: center;
  opacity: 0;
  //z-index: 1000;

  transition: opacity 0.3s ease-in-out;

  .module-selector-container {
    //display: flex;
    //flex-direction: column;
    //min-width: 600px;
    //max-width: 800px;
    //height: calc(100vh - 200px);

    .filter-container {
      ul {
        display: flex;

        li {
          text-transform: uppercase;
          background: $cluevo-gray;
          color: $cluevo-pink;
          padding: 10px;
          margin-left: 10px;
          font-weight: bold;
          cursor: pointer;

          &:first-child {
            margin-left: 0;
          }

          &.active {
            color: $cluevo-teal;
          }
        }
      }

      .search-container {
        display: flex;
        align-items: baseline;

        .cluevo-module-result-count {
          margin-left: auto;
        }

        label {
          display: flex;
          flex-direction: column;

          input {
            max-width: 250px;
          }
        }
      }
    }

    .module-table-container {
      height: 500px;
      overflow-y: auto;
      overflow-x: visible;

      .cluevo-admin-table {
        width: 100%;
        position: relative;

        tbody {
          tr {
            cursor: pointer;

            td.name {
              width: 100%;
            }
            td.type {
              white-space: nowrap;
            }
          }
        }
      }
    }
  }
}
