@import '_variables.scss';

.level-settings {
  display: flex;
}

.level-title-input-container {
  display: flex;

  textarea {
    margin-right: 5px;
    max-height: 200px;
  }

  .explanation {
    display: flex;
    flex-direction: column;

  }
}

.exp-table-container {
  max-height: 180px;
  max-width: 300px;
  overflow-y: scroll;

  table {
    max-height: 200px;
    width: 150px;
    tr {
      td, th {
        padding: 2px;
        text-align: right;
        white-space: nowrap;
      }
    }
  }
}
.exp-table {
  font-family: monospace;
  text-align: right;
  tr {
    td, th {
      white-space: nowrap;
    }
  }
}

.cluevo-admin-page-container.cluevo-general-settings {

  details {
    summary {
      cursor: pointer;
      padding: 10px 0 10px 10px;
      font-size: 16px;
      text-transform: uppercase;
      background: $cluevo-gray;
      color: white;
    }

    .cluevo-setting-sub-section-content {
      padding: 10px;

      .cluevo-setting {
        margin-bottom: 40px;

        .cluevo-setting-content {
          display: flex;
          align-items: flex-start;

          h3 {
            margin: 0 10px 0 0;
            text-align: right;
            //min-width: 150px;
            //max-width: 200px;
            width: 300px;
          }

          .cluevo-admin-table {
            margin-top: 0;
          }

          input[type="number"] {
            width: 80px;
          }

          .cluevo-radio-group {
            display: flex;
            flex-direction: column;

            div {
              display: flex;
              align-items: center;
              label {
                margin-right: 10px;
              }
            }
          }

        }
      }
    }
  }

  .cluevo-settings-save-button {
    margin: 50px 0 0 0;
    float: right;
  }

}
