div.competence-select-container, div.module-select-container {
  display: flex;
  flex-direction: column;

  table {
    max-width: 50%;
    td {
      padding: 0;
      vertical-align: middle;
      &.module-name {
        width: 120px;
      }
      input[type="number"] {
        max-width: 55px;
      }
    }
  }
}

div#competence-vue, div#competence-area-vue {
  display:flex;

  .comp-table-container {
    .cluevo-admin-table {
      .tools {
        display: flex;
        padding: 0;
        justify-content: flex-end;
        flex: 1;
      }
    }
  }

  div.w-70 {
    max-width: 70%;
  }

  div.w-30 {
    width: 30%;
    margin-left: 20px;

    #competence-edit-container, #competence-area-edit-container {
      background: #FFFFFF;
      box-shadow: $box-shadow;
      padding-bottom: 20px;

      h3 {
        margin-top: 0;
        border-bottom: 2px solid $cluevo-pink;
        background: $cluevo-gray;
        color: #FFFFFF;
        padding: 5px;
      }

      form {
        padding: 0 5px;

        .input-field {
          display: flex;
          align-items: baseline;
          margin-bottom: 10px;

          &.submit {
            justify-content: flex-end;
            padding: 0;
            margin: 0;
          }

          label {
            margin-right: 10px;
          }

          label:first-child {
            width: 50px;
            margin-right: 0;
          }
        }
      }

      h4, h5 {
        border-bottom: 2px solid $cluevo-purple;
        padding: 5px;
        background: $cluevo-gray;
        color: #FFFFFF;
        margin: 10px 0 0 0;
      }

      h5 {
        border-bottom: 2px solid $cluevo-teal;
        margin: 0;
      }

      .competence-details-container, .competence-area-details-container {
        display: flex;
        justify-content: space-between;

        div.modules, div.areas {
          width: 50%;
          ul {
            padding: 5px;
            list-style-position: inside;
            li {
              list-style-type: square;
            }

            &.edit-areas, &.edit-comps, &.edit-modules {
              li {
                list-style-type: none;
                ul {
                  margin-left: 20px;
                }
              }

              label {
                display: inline-block;
                max-width: 100%;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
            }
          }

          div.buttons {
            padding-left: 5px;
          }
        }
      }
    }

    p.hint {
      padding-left: 5px;
    }
  }
}

.competence-editor {

  h4 {
    margin-top: 0;
    border-bottom: 2px solid $cluevo-pink;
    background: $cluevo-gray;
    color: #FFFFFF;
    padding: 5px;
  }

  form {
    padding: 0 5px;

    .input-field {
      display: flex;
      align-items: baseline;
      margin-bottom: 10px;

      &.submit {
        justify-content: flex-end;
        padding: 0;
        margin: 0;
      }

      label {
        margin-right: 10px;
      }

      label:first-child {
        width: 50px;
        margin-right: 0;
      }
    }
  }

  h4, h5 {
    border-bottom: 2px solid $cluevo-purple;
    padding: 5px;
    background: $cluevo-gray;
    color: #FFFFFF;
    margin: 10px 0 0 0;
  }

  h5 {
    border-bottom: 2px solid $cluevo-teal;
    margin: 0;
  }

  table.name {
    width: 100%;

    tr {
      td {
        width: 10%;
      }

      td.input{
        width: 100%;
        input[type="text"] {
          width: 100%;
        }
      }
    }
  }


  .details-container {
    display: flex;
    justify-content: space-between;
    //overflow: auto;
    //max-height: 500px;

    div.modules, div.areas, div.competences {
      width: 50%;
      padding: 5px;
      box-sizing: border-box;

      table {
        width: 100%;
        table-layout: fixed;

        .right {
          text-align: right;
        }
        .left {
          text-align: left;
        }

        th, td {
          text-align: left;

          &.ellipsis {
            overflow: hidden;
            text-overflow: ellipsis;
          }

          input[type="number"] {
            max-width: 60px;
          }

          label {
            display: inline-block;
            width: 100%;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;

            input[type="checkbox"] {
              margin: 0 5px 0 0;
            }
          }
        }
      }

      ul {
        padding: 5px;
        list-style-position: inside;
        li {
          list-style-type: square;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }

        &.edit-areas, &.edit-comps, &.edit-modules {
          li {
            list-style-type: none;
            ul {
              margin-left: 20px;
            }
          }

        }
      }

      div.buttons {
        margin-top: 10px;
        padding-left: 5px;
      }
    }
  }
}
