$outline-background: #e2e2e2;
$meta-background-color: #f2f2f2;
$muted-border-color: #dadada;
$first-level-border-color: #d2d2d2;
$hover-background-color: #c3c3c3;
$icon-color: #444444;
$tree-max-width: 600px;

.mjs-nestedSortable-error {
  background: #ffb2b2 !important;
  border: 2px dashed #ff6262 !important;
}

@keyframes outline-glow {
  100% {
    background-color: #eaeaea;
  }
}

#reset-dependencies {
  margin-left: auto;
  margin-right: 10px;
}

@keyframes arrow-bounce {
  100% {
    left: -15px;
  }
}

li#item-tpl, .meta-container.template {
  display: none;
  height: 0;
  overflow: hidden;
}

.course-structure {
  * {
    box-sizing: border-box;
  }
}

ol.collapsed {
  height: 0;
}

ol {
  transition: height .3s ease-in-out;
  overflow: hidden;
}

ol.course-structure {
  margin: 20px 0 0 0;
  padding: 0;

  ol {
    margin:  0 0 10px;
    min-height: 20px;

    ol {
      margin: 0 0 0 10px;
      padding: 0;
      min-height: 20px;

      ol {
        height: 0;
        min-height: 0;
      }
    }

    li:last-child {
      li:last-child {
        margin-bottom: 10px !important;
      }
    }
  }

  li.lms-tree-item {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    position: relative;

    &.draft {
      opacity: 0.7;
      .draft {
        opacity: 1;
      }
    }
    .has-dependencies-icon {
      display: none;
      margin-right: 20px;
    }

    &.has-dependencies {
      .has-dependencies-icon {
        display: block;
      }
    }

    span.cluevo-item-is-link {
      display: none;
      margin: 0 10px;
    }

    &[data-item-is-link="1"] {
      span.cluevo-item-is-link {
        display: block;
      }

      .cluevo-btn.cluevo-make-module {
        display: none;
      }
    }

    //input[type="text"].sortable-name {
    //min-width: 70%;
    //}

    &.hover:after {
      content: '';
      width: 100%;
      height: 4px;
      background: $cluevo-purple;
      position: absolute;
      bottom: 0;
      margin-left: 15px;
    }

    &.module-assigned {
      .handle {
        background: linear-gradient(to right, $cluevo-pink 0%, #f7f7f7 15%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        background-position-x: 0%;
        background-size: 100%;
        background-repeat: no-repeat;
      }
      &:hover {
        .handle {
          background: linear-gradient(to right, $cluevo-pink 0%, #eaeaea 15%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        }
      }
    }

    li.lms-tree-item {
      position: relative;

      &.hover:after {
        content: '';
        background: $cluevo-teal;
        margin-left: 20px;
        bottom: 0;
      }

    }

    .cluevo-add-tree-child {
      margin: 0;
      margin-left: auto;
    }

    .placeholder {

      h1 {
        font-size: 1.2rem;
        margin: 0 0 5px 0;
        padding: 0;
        font-weight: bold;
      }
      p {
        margin: 0;
      }

      padding: 10px;
      outline: 1px dashed $cluevo-teal;
      display: flex;
      justify-content: space-around;
      align-items: center;
      flex-direction: column;
    }

    &.outline {
      position: relative;

      &:before {
        position: absolute;
        content: 'ðŸ¡†';
        left: -7px;
        top: 5px;
        font-size: 20px;
        animation: arrow-bounce 1s ease-in-out infinite alternate;
      }
    }

    .handle {
      display: flex;
      align-items: stretch;
      background: #F7F7F7;
      border-left: 4px solid $cluevo-pink;

      background-repeat: no-repeat;
      background-size: 70%;
      background-position-x: -30%;
      transition: background-position-x 0.7s ease-in-out;

      .title {
        display: flex;
        align-items: center;
        padding: 3px;
        min-width: 50%;

        input[type="text"] {
          width: 100%;
        }

        .type {
          margin-left: 5px;
          font-size: 12px;
          font-style: italic;
        }

        .shortcode {
          cursor: pointer;
          font-family: monospace;
          margin-left: 5px;
          font-size: 12px;
        }

        .fade {
          opacity: 0;
          transition: opacity .3s ease-in-out;
        }

        .cluevo-make-module {
          white-space: nowrap;
          &.hidden {
            display: none;
          }
        }

        .module-name-label {
          white-space: nowrap;
          box-shadow: $box-shadow;
          padding: 0;
          background: #fff;
          margin-left: 10px;
          display: flex;
          align-items: center;
          height: 27px;

          &.hidden {
            display: none;
          }

          .label-icon, .label-icon-right {
            background: $cluevo-gray;
            color: #fff;
            padding: 2px;
            height: 27px;
            width: 27px;
            display: flex;
            align-items: center;
            justify-content: center;
          }

          .label-icon-right {
            //color: $cluevo-gray;
            //background: $cluevo-light-gray;
          }

          .content {
            margin: 0px 5px;
          }

          .remove-module, .cluevo-edit-module {
            cursor: pointer;
          }
        }

      }

      .drag-handle {
        content: '....';
        align-self: center;
        width: 10px;
        height: 25px;
        display: inline-block;
        overflow: hidden;
        line-height: 5px;
        box-sizing: content-box;
        padding: 2px 0px;
        cursor: move;
        vertical-align: middle;
        margin-top: -0.5em;
        margin-right: 0.3em;
        margin-left: 2px;
        font-size: 12px;
        font-family: sans-serif;
        letter-spacing: 2px;
        color: #cccccc;
        text-shadow: 1px 0 1px black;
        &:hover {
          background: transparent;
        }
        &:after {
          content: '.. .. .. .. ..';
        }
      }

      .buttons {
        display: flex;
        align-items: center;
        margin-left: auto;

        .cluevo-btn {
          color: #FFFFFF;
          min-height: 35px;
          min-width: 35px;
          margin: 0;
          font-size: 16px;

          &.expand {
            //&:before {
            //content: '\25BC';
            //transition: all 0.4s ease-in-out;
            //}
            span {
              transition: all 0.4s ease-in-out;
            }

            &.collapsed {
              span {
                transform: rotate(-90deg);
              }
            }
          }

          &.metadata-edit-link {
            text-decoration: none;
            color: #FFFFFF;
            //&:before {
            //font-family: dashicons;
            //content: "\f120";
            //}
          }

        }
      }
    }

    &:hover {
      > .handle {
        background: #EAEAEA;
        &:hover {
          .fade {
            opacity: 1;
          }
        }
      }
    }


    .meta {
      //display: none;
      background: #F5F5F5;
      color: $cluevo-gray;
      transition: height .3s ease-in-out;
      overflow: hidden;
      height: 0;

      &.active {
        display: flex;
        flex-direction: column;
      }

      .meta-content-container {
        margin-left: 4px;
        padding: 10px 20px;

        h2 {
          margin: 0 0 10px 0;
          color: $cluevo-gray;
          font-size: 16px;
          padding: 0 0 4px 0;
          border-bottom: 2px solid #FFFFFF;
        }

        .meta-container {
          margin-bottom: 10px;
          padding-bottom: 10px;
          border-bottom: 2px solid #FFFFFF;

          .label {
            &.sub {
              margin-bottom: 5px;
            }
          }

          table.cluevo-meta-settings-group {
            tr {
              td:first-child {
                padding-right: 5px;
              }
            }
          }

          .help {
            font-size: 11px;
            margin: 0 0 4px 0;
          }

          .input-container.iframe-position {
            display: none;
            &.visible {
              display: block;
            }
          }

          .input-list-container {
            display: flex;
            align-items: baseline;

            .input-container {
              display: flex;
              flex-direction: column;
              justify-content: flex-start;
              align-items: baseline;
              margin-right: 5px;

              label {
                margin-right: 5px;
                font-size: 12px;
              }
            }
          }

          &.dependency-container {
            .dep-checkbox-container {
              display: flex;
              padding: 0;
              justify-content: flex-start;

              fieldset {
                margin-right: 20px;

                legend {
                  border-bottom: 2px solid #FFFFFF;
                  display: block;
                  width: 100%;
                  margin-bottom: 10px;
                }
              }

              label.dependency {
                margin: 0 !important;
                display: block;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
            }
          }

        }
      }
    }

    li.lms-tree-item {
      margin: 0;
      padding-left: 10px;
      margin-top: 15px;

      &.module-assigned {
        .handle {
          background: linear-gradient(to right, $cluevo-purple 0%, #f7f7f7 15%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
          background-position-x: 0%;
          background-size: 100%;
          background-repeat: no-repeat;
        }
        &:hover {
          .handle {
            background: linear-gradient(to right, $cluevo-purple 0%, #eaeaea 15%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
          }
        }
      }

      .handle {
        border-left-color: $cluevo-purple;
        background-repeat: no-repeat;
        background-size: 70%;
        background-position-x: -30%;
        transition: background-position-x 0.7s ease-in-out;

      }

      &:hover {
        > .handle {
          background: #EAEAEA;
        }
      }

      li.lms-tree-item {

        &.module-assigned {
          .handle {
            background: linear-gradient(to right, $cluevo-teal 0%, #f7f7f7 15%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            background-position-x: 0%;
            background-size: 100%;
            background-repeat: no-repeat;
          }
          &:hover {
            .handle {
              background: linear-gradient(to right, $cluevo-teal 0%, #eaeaea 15%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            }
          }
        }
        .handle {
          border-left-color: $cluevo-teal;
          background-repeat: no-repeat;
          background-size: 70%;
          background-position-x: -30%;
          transition: background-position-x 0.7s ease-in-out;
        }

        &:hover {
          > .handle {
            background: #EAEAEA;
          }
        }

      }
    }
  }
}

.shortcode-copy-notification {
  background: rgba(255, 255, 255, 0.8);
  box-shadow: $box-shadow;
  padding: 2px 5px;
}

.course-structure-buttons {
  display: flex;
  justify-content: flex-end;

  .cluevo-inc-btn-size {
    margin: 0 auto;
    padding: 20px !important;
    background: $cluevo-gray !important;
  }
}

.cluevo-course-structure-tools {
  display: flex;
  justify-content: space-between;
  align-items: center;

  input {
    max-height: 25px;
  }
}

.plugin-page-title-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;

  .plugin-logo {
    justify-self: flex-end;
  }
}

.course-group-selection-container {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  margin: 10px 0 0 0;

  label {
    display: flex;
    flex-direction: column;
    font-size: .9em;
  }

  p {
    margin: 15px 10px 0 10px;
  }
}

#tree-form {
  hr {
    border: none;
    height: 1px;
    margin: 20px 0;
    background: $cluevo-light-gray;
  }
}

p.cluevo-add-demos {
  font-size: 10px;
}
