@import '_variables.scss';

table.cluevo-scorm-modules {
  tr {
    td, th {
      &.module-name {
        width: 70%;
        text-align: left;
      }

      &.module-tools {
        .module-tools-container {
          display: flex;

          a, p {
            margin-left: 10px;
          }
        }
      }
    }
  }
}

input.module-lang-upload {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

label.module-upload-label {
  display: inline-block;
}

label.cluevo-module-install-type-file {
  font-size: unset;

  input {
  display: none;
  }
}

div.cluevo-module-install-fields {
  label {
    display: none;
  }

  &.file {
    .cluevo-module-install-type-file {
      display: block;
    }
  }
  &.url {
    .cluevo-module-install-type-url {
      display: flex;
    }
  }
}

.cluevo-module-upload-form-buttons {
  display: flex;
  flex-direction: row;
  align-items: center;

  p {
    margin: 0;
  }
}

#module-lang-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, .8);

  .module-lang-select-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;

    .module-lang-select {
      background: #F5F5F5;
      box-shadow: $box-shadow;
      padding: 20px;
    }
  }
}

.cluevo-module-form {
  margin-top: 20px;
  display: flex;
  align-items: center;
}

.input-switch {
  display: flex;
  align-items: center;
  position: relative;
  width: 400px;

  input[type="text"] {
    width: 100%;
    padding: 4px 120px 4px 4px;
  }

  .cluevo-selected-file {
    position: absolute;
    left: 2px;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  label {
    position: absolute;
    right: 0;
    .cluevo-btn {
      padding-top: 0;
      padding-bottom: 0;
    }

  }
}

.cluevo-add-module-overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  display: none;
  justify-content: center;
  align-items: center;

  .modal-container {
    .modal-body {
      overflow: auto;
    }
  }

  .cluevo-add-module-content-container {
    background: #fff;
    position: relative;
    overflow: auto;

    hr {
      margin: 25px 0;
    }

    h1 {
    }

    .module-list {
      display: flex;
      flex-wrap: wrap;

      .module-type {
        width: 120px;
        height: 120px;
        border: 1px solid $cluevo-gray;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-bottom: 10px;
        margin-right: 10px;
        padding: 10px 10px 0 10px;
        position: relative;

        &:hover {
          cursor: pointer;
          background: $cluevo-light-gray;
          color: #fff;
        }

        &.extension {
          &:after {
            content: '';
            width: 0;
            height: 0;
            position: absolute;
            top: 0;
            left: 0;
            border-style: solid;
            border-width: 32px 32px 0 0;
            border-color: $cluevo-teal transparent transparent transparent;
          }
          .module-icon {
            img {
              filter: grayscale(1);
            }
          }
        }

        .module-type-name {
          width: 100%;
          overflow: hidden;
          text-align: center;
          white-space: nowrap;
          text-overflow: ellipsis;
        }

        .module-icon {
          //margin: 10px 5px 5px 5px;
          width: 80%;
          height: 80%;
          margin-bottom: 5px;

          img {
            height: 100%;
            width: auto;
          }
        }
      }
    }

    .cluevo-btn.select-type {
      display: none;
      margin-left: auto;
      margin-top: 20px;
    }

    .cluevo-btn.continue, .cluevo-btn.force {
      display: none;
      margin-left: auto;
      margin-top: 20px;
    }

    .upload-progress {
      display: none;
      .cluevo-progress-container {
        height: 10px;
      }
      .result-container {
        padding-right: 10px;
      }
    }

    .module-description-container {
      .module-type {
        display: none;

        &.hint {
          display: block;
        }

        .description-container {
          display: flex;
          padding-bottom: 20px;
          border-bottom: 1px solid $cluevo-gray;

          .module-icon {
            max-width: 128px;
            height: auto;
            margin-right: 20px;

            img {
              width: 100%;
            }
          }

          div {
            h3 {
              margin-top: 0;
            }
            p {
              margin-top: 0;
            }
          }
        }
      }

      .cluevo-add-module-alt-content {
        .cluevo-btn-install-type {
          font-size: 1.2em;
          padding: 20px;
          display: block;
          margin: 0 auto;
        }
      }
    }

    .cluevo-btn.close {
      position: absolute;
      top: 10px;
      right: 10px;
    }
  }
}
