@keyframes pulse {
  0% { box-shadow: 0 0 0 0 rgba($cluevo-pink, 1); }
}

@keyframes cluevo-button-line-hover {
  0% {
    background-color: $cluevo-pink;
  }

  40% {
    background-color: $cluevo-purple;
  }

  70% {
    background-color: $cluevo-teal;
  }

  100% {
    background-color: $cluevo-pink;
  }
}

.nav-tab-wrapper {
  &.cluevo {
    .nav-tab {
    }
    .nav-tab-active {
      background: $cluevo-gray;
      color: #FFFFFF;
    }
  }
}

#wpfooter {
  position: static;
}

.cluevo-admin-page-container {
  margin: 0;
  padding: 0;

  .cluevo-admin-page-title-container {
    background: #23282d;
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;

    h1 {
      color: #FFFFFF;
    }
  }
  .cluevo-admin-page-content-container {
    padding: 10px 20px;
  }
}


#cluevo-lms-update {
  p.dummy {
    display: none;
  }
}
.cluevo-plugin-update-info {
  .cluevo-update-compat-text {
    margin: 10px 0;
  }

  table.cluevo-ext-update-info {
    border-spacing: 0 !important;
    border-collapse: collapse;
    margin-bottom: 10px;
    tr {
      th {
        font-weight: bolder;
        box-shadow: inset 0 -1px 0 rgba(0,0,0,.1);
      }
      td {
        border: 0 !important;
        background: transparent !important;
        box-shadow: none !important;
      }
    }
  }
}
