table.cluevo-admin-table {
  border-spacing: 0;
  margin-top: 20px;
  font-size: 14px;

  .right {
    text-align: right;
  }

  .left {
    text-align: left;
  }

  .uppercase {
    text-transform: uppercase;
  }

  td,
  th {
    text-align: right;
    padding: 5px 10px;
    font-weight: normal;
    vertical-align: middle;
    &:first-child {
      border-left: 5px solid transparent;
    }
  }

  th {
    border-bottom: 2px solid $cluevo-pink;
    //animation: cluevo-th-border 60s infinite;
    background: $cluevo-gray;
    color: #ffffff;

    &:first-child {
      padding-left: 15px;
      border-left: none;
    }
  }

  tr {
    &:not(.filter-row):hover,
    &.active {
      td:first-child {
        border-left: 5px solid $cluevo-pink;
        animation: cluevo-progress-td-hover 10s infinite;
      }

      td {
        background-color: #f5f5f5;
        //color: #FFFFFF;
      }
    }

    &.no-hover:hover {
      td:first-child {
        border-left: none !important;
        animation: none;
      }

      td {
        background-color: inherit;
      }
    }

    &:nth-child(2n) {
      background-color: #e6e6e6;
    }

    &.bordered {
      td {
        border-top: 2px solid $cluevo-gray;
      }
    }

    td {
      a {
        text-decoration: none;
      }

      &.cluevo-table-filter {
        cursor: pointer;
        font-weight: bold;
      }

      &.tools {
        display: flex;
        a {
          margin-left: 5px;
        }
      }

      .tools {
        display: flex;
      }

      &.status {
        font-weight: bold;

        &.passed,
        &.completed {
          color: #00ff00;
        }
        &.unknown,
        &.incomplete {
          color: orange;
        }
        &.failed {
          color: #ff0000;
        }

        &.invalid {
          color: #C7C7C7;
        }
      }

      &.credit-status {
        &.credit {
          color: #00ff00;
        }
        &.no-credit {
          color: #ff0000;
        }
      }
    }
  }
}

.cluevo-admin-table-pagination {
  display: flex;
  align-items: center;

  a.button {
    text-decoration: none;
    margin: 10px;
  }

  .cluevo-select-page {
    display: flex;
    align-items: baseline;

    select {
      margin: 20px 10px;
    }
  }
}

p.cluevo-admin-warning {
  font-size: 1.3em;
  color: orange;
  font-weight: bold;
}

@keyframes cluevo-progress-td-hover {
  0% {
    border-left-color: $cluevo-pink;
  }

  40% {
    border-left-color: $cluevo-purple;
  }

  70% {
    border-left-color: $cluevo-teal;
  }

  100% {
    border-left-color: $cluevo-pink;
  }
}

@keyframes cluevo-th-border {
  0% {
    border-bottom-color: $cluevo-pink;
  }

  40% {
    border-bottom-color: $cluevo-purple;
  }

  70% {
    border-bottom-color: $cluevo-teal;
  }

  100% {
    border-bottom-color: $cluevo-pink;
  }
}
