.user-admin-app {
  .buttons {
    display: flex;
    justify-content: flex-start;

    .btn {
      margin-right: 5px;
      width: auto;
      padding: 4px 10px;
    }
  }

  .legend {
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
    margin-top: 20px;

    > div {
      display: flex;
      align-items: center;
      margin-right: 10px;
    }

    .trainer, .user {
      width: 16px;
      height: 16px;
      margin-right: 5px;
    }

    .trainer {
      background-color: $cluevo-pink;
    }

    .user {
      background-color: $cluevo-purple;
    }
  }

  table.cluevo-admin-table {
    tr.legend {
      &:hover {
      }
    }
  }
}

.show-user-list-enter-active {
  transition: all .5s ease-in-out;
}

.show-user-list-leave-active {
  transition: all .5s ease-in-out;
}

.show-user-list-enter {
  opacity: 0;
  transform: scale(1.5);
}

.show-user-list-leave {
  opacity: 1;
  transform: scale(1);
}

.show-user-list-leave-to {
  opacity: 0;
  transform: scale(1.5);
}

.show-user-list-enter-to {
  opacity: 1;
  transform: scale(1);
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.cluevo-group-badge-container {
  display: flex;
  background: $cluevo-gray;
  color: $cluevo-purple;
  margin-right: 4px;
  transition: all .5s ease-in-out;
  font-size: .9em;

  &.trainer {
    color: $cluevo-pink;
  }

  .cluevo-group-badge {
    padding: 4px 5px;
    cursor: pointer;
    user-select: none;
  }

  .editor {
    display: flex;
    margin-left: 4px;
    background: $cluevo-gray;

    button {
      font-size: .8em;
      background: $cluevo-gray;
      border: none;
      color: $cluevo-teal;
      white-space: nowrap;
      border-bottom: 2px solid transparent;
      cursor: pointer;
      display: flex;
      align-items: center;

      &:hover {
        border-bottom: 2px solid $cluevo-pink;
      }
    }
  }
}

.user-groups-cell {
  display: flex;
  justify-content: flex-start;

  .btn.add-to-group {
  }
}

.modal-body {
  .group-info {
    display: flex;
    flex-direction: column;

    label {
      display: flex;
      flex-direction: column;
    }
  }
}
