.permission-admin-app {
  .selectors {
    display: flex;
    margin-bottom: 20px;
    padding-bottom: 10px;
    //border-bottom: 1px solid $cluevo-light-gray;

    .group-user-separator {
      margin: 0 10px;
    }

    label {
      display: flex;
      flex-direction: column;
    }
  }

  fieldset {
    border: 1px solid $cluevo-light-gray;
    padding: 10px;
    width: auto;
    display: inline-block;
    margin-left: auto;

    div {
      display: flex;
      align-items: center;
      margin-bottom: 5px;

      &.types {
        div {
          margin-right: 10px;
        }
      }

      span {
        width: 20px;
        height: 20px;
        display: inline-block;
        margin-right: 5px;

        &.course {
          background: $cluevo-pink;
        }
        &.chapter {
          background: $cluevo-purple;
        }
        &.module {
          background: $cluevo-teal;
        }
      }
    }
  }
  .permissions-container {
    .perm-items {
      margin: 20px 0;
      .main-item-container {
        display: flex;
        border-bottom: 1px solid $cluevo-light-gray;
        padding-bottom: 5px;
        margin-bottom: 5px;

        h2 {
          margin: 0 0 0 10px;
          padding: 0;
          border: none;
        }
      }

      .lms-perm-item {
        margin-left: 10px;
        display: flex;
        flex-direction: column;
        padding: 10px 0 0 10px;

        &.level-0 {
          .perm-title {
            border-left-color: $cluevo-gray;
          }
        }

        &.level-1 {
          padding-bottom: 10px;

          &:hover {
            >.perm-title {
              >.access-level-container:before {
                //background: linear-gradient(to right, rgba(209,9,122,1) 0%,rgba(255,255,255,0.44) 56%,rgba(255,255,255,0) 100%); [> W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ <]
                border-bottom: 2px solid $cluevo-pink;
                width: 100%;
              }
            }
          }
          .perm-title {
            border-left-color: $cluevo-pink;
          }
        }
        &.level-2 {
          &:hover {
            >.perm-title {
              >.access-level-container:before {
                //background: linear-gradient(to right, rgba(137,116,174,1) 0%,rgba(255,255,255,0.44) 56%,rgba(255,255,255,0) 100%); [> W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ <]
                border-bottom: 2px solid $cluevo-purple;
                width: 100%;
              }
            }
          }
          .perm-title {
            border-left-color: $cluevo-purple;
          }
        }
        &.level-3 {
          &:hover {
            >.perm-title {
              >.access-level-container:before {
                //background: linear-gradient(to right, rgba(60,233,232,1) 0%,rgba(255,255,255,0.44) 56%,rgba(255,255,255,0) 100%); [> W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ <]
                border-bottom: 2px solid $cluevo-teal;
                width: 100%;
              }
            }
          }
          .perm-title {
            border-left-color: $cluevo-teal;
          }
        }

        .perm-title {
          display: flex;
          border-left-width: 5px;
          border-left-style: solid;
          align-items: baseline;

          .module-warning {
            span.dashicons {
              color: orange;
            }
            margin-left: 10px;

            .effective-group {
              text-decoration: underline;
              cursor: pointer;
            }
          }

          .access-level-container {
            z-index: 1;
            position: relative;
            display: flex;
            align-items: baseline;


            &:before {
              position: absolute;
              width: 0;
              height: 100%;
              content: '';
              z-index: -1;
              left: -5px;
              transition: width .2s ease-in-out;
            }
          }

          .perm-item-name {
            margin-left: 10px;
            font-size: 1.1em;
          }
        }
      }
    }
  }

  .preview-container {
    .lms-preview-item {
      margin-left: 10px;
    }
  }

  .access-level-container {
    input { display: none; }
    label {
      margin-right: 5px;
      color: $cluevo-light-gray;
      opacity: .8;
      &.active {
        color: $cluevo-gray;
        opacity: 1;
      }

      &.effective {
        color: orange;
      }
    }
  }
}
