.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 700px;
  min-height: 200px;
  margin: 0px auto;
  padding-bottom: 20px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
  display: flex;
  flex-direction: column;
  max-height: calc(100vh - 100px);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid $cluevo-pink;
  background: $cluevo-gray;
  min-height: 32px;
  align-items: center;
  box-sizing: border-box;

  h3 {
    width: 100%;
    margin: 0;
    color: #FFFFFF;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 5px;
    height: 100%;
    box-sizing: border-box;
  }

  button {
    width: 32px;
    background: $cluevo-gray;
    color: #FFFFFF;
    //border: 2px solid $cluevo-light-gray;
    border: none;
    outline: none;
    transition: all .2s ease-in-out;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
    align-self: stretch;

    &:hover {
      //border-color: $cluevo-pink;
      color: $cluevo-pink;
    }
  }
}

.modal-body {
  margin: 20px 30px;
  flex: 1;
  overflow: auto;
  .cluevo-admin-table.limit {
    thead, tbody {
      tr {
        display: flex;
        width: 500px;

        td {
          box-sizing: content-box;
        }

        .check {
          width: 30px;
        }
        .id {
          width: 100px;
        }
        .name {
          width: 100%;
        }
      }
    }
    tbody {
      display: block;
      max-height: 400px;
      overflow-y: auto;
    }
  }
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  padding: 20px 25px 10px 0;
  border-top: 1px solid $cluevo-light-gray;
}

.modal-default-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

  .modal-enter {
    opacity: 0;
  }

  .modal-leave-active {
    opacity: 0;
  }

  .modal-enter .modal-container,
  .modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }
